import * as store from 'store';

const POS_GA_SESSION_ID_STORE_KEY = 'GA_SESSION_ID';

//We must always pass some posGaSessionId to the backend, otherwise it will send the event missing
//cid field to the server, which will be ignored
export const generateFallbackPosGaSessionId = () => {
    let sessionId = store.get(POS_GA_SESSION_ID_STORE_KEY);
    if (!sessionId) {
        //Imitating the session id format used by GA itself
        sessionId = Math.floor(Math.random() * 9999999999 + 1) + '.' + Math.floor(Math.random() * 9999999999 + 1);
        store.set(POS_GA_SESSION_ID_STORE_KEY, sessionId);
    }
    return sessionId;
};
