import { OptionalKeys } from './type-utils';

//how to convert enum into union type: https://stackoverflow.com/questions/52393730/typescript-string-literal-union-type-from-enum
const lit = <V extends keyof any>(v: V) => v;
export const TipserEnv = {
    prod: lit('prod'),
    stage: lit('stage'),
    dev: lit('dev'),
    localhost: lit('localhost'),
};

export type TipserEnv = typeof TipserEnv[keyof typeof TipserEnv];

export const TipserLang = {
    enUS: lit('en-US'),
    deDE: lit('de-DE'),
    frFR: lit('fr-FR'),
    svSE: lit('sv-SE'),
    fiFI: lit('fi-FI'),
    nlNL: lit('nl-NL'),
};

export type TipserLang = typeof TipserLang[keyof typeof TipserLang];

export interface ModalUiSettings {
    hideSearchIcon?: boolean;
    hideFavouritesIcon?: boolean;
    hideCartIcon?: boolean;
    hideMoreIcon?: boolean;
    hideSimilarProducts?: boolean;
    useCustomCss?: boolean;
}

export interface TipserWidgetConfig {
    userid?: string;
    primaryColor?: string;
    modalUi?: ModalUiSettings;
    lang: TipserLang;
    redirectToQueryString?: boolean;
    position?: { left?: string; right?: string; top?: string; bottom?: string };
    posData?: any;
}

//Tipser SDK config is a classic tipser widget config extended with some SDK-specific properties
export interface TipserSdkConfig extends TipserWidgetConfig {
    posId: string;
    env?: TipserEnv;
}

type KeysWithoutDefaultValues = 'posData' | 'userid' | 'primaryColor' | 'redirectToQueryString';
type ConfigDefaults = Required<Pick<TipserSdkConfig, Exclude<OptionalKeys<TipserSdkConfig>, KeysWithoutDefaultValues>>>;
export type TipserSdkConfigCore = TipserSdkConfig & ConfigDefaults;
//Initializing default configuration required for the supported functionality
const baseConfig: ConfigDefaults = {
    position: {},
    modalUi: {},
    env: TipserEnv.prod,
};

type UrlsPerEnv = { [key in TipserEnv]: string };

const TIPSER_FRONTEND_URLS_PER_ENV: UrlsPerEnv = {
    [TipserEnv.prod]: 'https://www.tipser.com',
    [TipserEnv.stage]: 'https://t3-stage.tipser.com',
    [TipserEnv.dev]: 'https://t3-dev.tipser.com',
    [TipserEnv.localhost]: 'http://localhost:3000',
};

export const initConfig = (posId: string, config: TipserSdkConfig): TipserSdkConfigCore => {
    return { ...baseConfig, userid: posId, posId, ...config };
};

export const getTipserFrontendUrl = (config: TipserSdkConfigCore) => {
    return TIPSER_FRONTEND_URLS_PER_ENV[config.env];
};
