type stashedLogEntry = { methodName: string; args: string[]; date: Date; windowLocation: string; context: string };

let isLoggerOn = false;
let logsStash: stashedLogEntry[] = [];

type ConsoleMethod = (message?: any, ...optionalParams: any[]) => void;

class BaseLogger {
    log: ConsoleMethod;
    error: ConsoleMethod;
    debug: ConsoleMethod;
    warn: ConsoleMethod;
    info: ConsoleMethod;
}

class Logger extends BaseLogger {
    context: string = undefined;

    constructor() {
        super();
        const methodNames = ['log', 'error', 'debug', 'warn', 'info'];
        methodNames.forEach(
            methodName =>
                (this[methodName] = (...args: any[]) => {
                    const shortenedPathName = window.location.pathname.replace(/\/.*\//, '/(...)/');
                    const hostname = window.location.origin;
                    const windowLocation = hostname + shortenedPathName;
                    if (this.isOn) {
                        applyToConsoleLog({ methodName, context: this.context, args, windowLocation });
                    } else {
                        this.logsStash.push({ methodName, date: new Date(), args, windowLocation, context: this.context });
                    }
                })
        );
    }

    get isOn(): boolean {
        return isLoggerOn;
    }
    get logsStash(): stashedLogEntry[] {
        return logsStash;
    }
    on() {
        isLoggerOn = true;
        logger.log('Logger is on!');
        printStash(this.logsStash);
        logsStash = [];
    }
    off() {
        logger.log('Logger is off!');
        isLoggerOn = false;
    }
    createNewContext(context: string) {
        const newInstance = new Logger();
        newInstance.context = context;
        return newInstance;
    }
}

export const logger: Logger = new Logger();

declare global {
    interface Window {
        TipserLogger: { isOn: () => boolean; on: () => void; off: () => void };
    }
}

const isCurrentWindowTop = window && window.top === window;
const isWindowInFrame = window && window.top !== window;

if (isCurrentWindowTop) {
    window.TipserLogger = {
        isOn() {
            return logger.isOn;
        },
        on() {
            const iframes = document.querySelectorAll('iframe');
            Array.from(iframes).forEach(iframe => iframe.contentWindow.postMessage({ message: 'TipserLogger.On' }, '*'));
            logger.on();
        },
        off() {
            const iframes = document.querySelectorAll('iframe');
            Array.from(iframes).forEach(iframe => iframe.contentWindow.postMessage({ message: 'TipserLogger.Off' }, '*'));
            logger.off();
        },
    };

    window.addEventListener('message', event => {
        if (event.data && event.data.message === 'TipserLogger.isOn?' && logger.isOn) {
            if ('window' in event.source) {
                event.source.window.postMessage({ message: 'TipserLogger.On' }, '*');
            }
        }
    });
}

if (isWindowInFrame) {
    window.addEventListener('message', event => {
        if (!event.data) {
            return;
        }
        const { message } = event.data;
        if (message === 'TipserLogger.On') {
            logger.on();
        }
        if (message === 'TipserLogger.Off') {
            logger.off();
        }
    });

    window.top.postMessage({ message: 'TipserLogger.isOn?' }, '*');
}

function printStash(messagesStash: Logger['logsStash']) {
    messagesStash.forEach(({ methodName, args, date, windowLocation, context }) =>
        applyToConsoleLog({ methodName, args, date, windowLocation, context })
    );
}

function applyToConsoleLog({
    methodName,
    args,
    date,
    windowLocation,
    context,
}: {
    methodName: string;
    args: any[];
    windowLocation: string;
    context?: string;
    date?: Date;
}) {
    const finalArgs = [windowLocation, date ? date.toLocaleTimeString() : undefined, context ? `[${context}]` : undefined, ...args].filter(
        x => !!x
    );
    console[methodName].apply(console, finalArgs);
}
