const createIframe = (src: string): Promise<HTMLIFrameElement> => {
    const iframe = document.createElement('iframe');
    iframe.setAttribute('src', src);
    iframe.style.display = 'none';
    const onLoadPromise = new Promise<HTMLIFrameElement>(resolve => (iframe.onload = () => resolve(iframe)));
    document.body.appendChild(iframe);
    return onLoadPromise;
};

export const getOrCreateIframe = (src: string): Promise<HTMLIFrameElement> => {
    const existingIframe: HTMLIFrameElement | null = document.querySelector(`iframe[src="${src}"]`);
    if (existingIframe) {
        return Promise.resolve(existingIframe);
    }
    return createIframe(src);
};
