import { TipserLang, TipserEnv, TipserSdkConfig, TipserSdkConfigCore } from './config';

export type ValidatableConfig = {
    env?: TipserSdkConfig['env'];
    lang?: TipserSdkConfig['lang'];
    posId?: TipserSdkConfig['posId'];
    primaryColor?: TipserSdkConfig['primaryColor'];
};

export type ValidationConfigResult = {
    config: ValidatableConfig;
    isValid: boolean;
    error?: Error;
};

const mongoObjectIdRegExp = /^[a-f\d]{24}$/i;
const hexRegex = /^#[0-9a-f]{3}(?:[0-9a-f]{3})?$/i;
export function validateConfig(config: ValidatableConfig): ValidationConfigResult {
    const thingsToValidate: [ConfigValidatorFn, string][] = [
        [createIncludesValidator(Object.values(TipserLang)), 'lang'],
        [createIncludesValidator(Object.values(TipserEnv)), 'env'],
        [
            createRegexValidator({
                regex: mongoObjectIdRegExp,
                errorMessage: 'It should contain 24 characters, e.g. "45cbc4a0e4123f6920000002"',
            }),
            'posId',
        ],
        [
            createRegexValidator({
                regex: hexRegex,
                errorMessage: 'It should be in hex color format, e.g. #fff or #ffffff',
                allowEmptyValue: true,
            }),
            'primaryColor',
        ],
    ];

    for (const [validateFn, configKey] of thingsToValidate) {
        const { isValid, errorMessage } = validateFn(config[configKey]);
        if (!isValid) {
            return {
                isValid: false,
                error: new Error(`Config value ("${config[configKey]}") of "${configKey}" is invalid. ${errorMessage}`),
                config,
            };
        }
    }

    return { isValid: true, config };
}

export function isConfigValid(config: TipserSdkConfig, validationResult: ValidationConfigResult): config is TipserSdkConfigCore {
    if (config !== validationResult.config) {
        throw new Error('Validation result does not match config');
    }
    return validationResult.isValid;
}

interface ConfigValidatorFn {
    (value: any): { isValid: boolean; errorMessage?: string };
}

function createRegexValidator({
    regex,
    errorMessage,
    allowEmptyValue,
}: {
    regex: RegExp;
    errorMessage: string;
    allowEmptyValue?: boolean;
}): ConfigValidatorFn {
    return function validateAgainstRegexp(value: any): ReturnType<ConfigValidatorFn> {
        if (allowEmptyValue && !value) {
            return { isValid: true };
        }
        if (!regex.test(value)) {
            return { isValid: false, errorMessage: errorMessage || `It should match regexp ${regex.toString()}` };
        }

        return { isValid: true };
    };
}

function validateRequired(value: any): ReturnType<ConfigValidatorFn> {
    if (!value) {
        return { isValid: false, errorMessage: 'It is required.' };
    }
    return { isValid: true };
}

function createIncludesValidator<T>(values: T[]): ConfigValidatorFn {
    return function validateIncludes(value: T): ReturnType<ConfigValidatorFn> {
        if (!values.includes(value)) {
            const possibleValues = values.map(x => `'${x}'`).join(', ');
            return { isValid: false, errorMessage: `It should be one of ${possibleValues}` };
        }

        return { isValid: true };
    };
}
