import { getTipserFrontendUrl, TipserSdkConfigCore } from './config';
import { generateFallbackPosGaSessionId } from './ga-sessions';

export const dialogsManager = () => {
    const ua = navigator.userAgent;
    const isMobileSafari = /iPad|iPhone|iPod/.test(ua) && !(window as any).MSStream;

    const activeDialogs = <any[]>[];
    const activeOverlays = <any[]>[];

    let initialized = false;
    let bodyStyle = <string>undefined;
    let isDialogActive = false;
    let lastWindowYScrollPosition = 0;
    let hiddenBodyNodes = <any[]>[];

    const MOBILE_MAX_WIDTH = 979;

    const loaded = function(evt: { target: any }, config) {
        const iframe = evt.target;

        iframe.previousSibling.style.display = 'none';
        window.addEventListener('message', loadingListener, false);

        iframe.contentWindow.postMessage(
            {
                action: 'init',
                payload: {
                    posData: config.posData,
                },
                widgetHash: 'tipser-dialog', //hence there can be only one Tipser dialog per old widget host, this can be hardcoded
            },
            '*'
        );
    };

    const close = function() {
        isDialogActive = false;
        if (hasOverlays()) {
            const activeOverlay = getLastActiveOverlay();
            activeOverlay.parentNode.removeChild(activeOverlay);
            activeOverlays.splice(-1, 1);
        }

        if (hasDialogs()) {
            const activeDialog = getLastActiveDialog();
            activeDialog.parentNode.removeChild(activeDialog);
            activeDialogs.splice(-1, 1);
        } else {
            return;
        }

        if (!hasDialogs()) {
            document.getElementsByTagName('body')[0].style.overflow = bodyStyle;
        }

        // uglyhack for ios7 scrolling bug
        if (isMobileSafari) {
            restoreAllBodyChildNodes();
        }
    };

    const hideAllBodyChildNodes = function(overlay) {
        lastWindowYScrollPosition = window.scrollY;
        const children = document.body.childNodes;
        hiddenBodyNodes = [];
        for (let i = 0; i < children.length; ++i) {
            const childNode = children[i] as (HTMLElement & Text);
            if (childNode.wholeText) {
                hiddenBodyNodes.push({ text: true, restoringValue: childNode.wholeText, node: childNode });
            } else if (childNode !== overlay) {
                hiddenBodyNodes.push({ text: false, restoringValue: childNode.style && childNode.style.display, node: childNode });
                if (childNode.style) {
                    childNode.style.display = 'none';
                }
            }
        }
    };

    const restoreAllBodyChildNodes = function() {
        for (let i = 0; i < hiddenBodyNodes.length; ++i) {
            const data = hiddenBodyNodes[i];
            if (data.text) {
                // nothing to do here yet
            } else {
                if (data.node.style) {
                    data.node.style.display = data.restoringValue;
                }
            }
        }

        if (window.scrollTo) {
            window.scrollTo(0, lastWindowYScrollPosition);
        }
    };

    const getQueryString = function(from: string, posGaSessionId: string, qsParams: object, config: TipserSdkConfigCore) {
        const originalUrl = 'parentUrl=' + encodeURIComponent(window.location.href);
        const primaryColor = config.primaryColor ? '&primaryColor=' + encodeURIComponent(config.primaryColor) : '';
        const modalUi = '&modalUi=' + encodeURIComponent(JSON.stringify(config.modalUi));
        const lang = '&defaultLang=' + encodeURIComponent(config.lang);
        const source = from ? '&from=' + encodeURIComponent(from) : '';
        const gaSessionId = posGaSessionId ? '&posGaSessionId=' + encodeURIComponent(posGaSessionId) : '';
        const redirectToQueryString = config.redirectToQueryString ? '&paymentRedirectWithQueryString=true' : '';
        const referrer = document.referrer ? `&referrer=${encodeURIComponent(document.referrer)}` : '';
        const extraParams = qsParams
            ? '&' +
              Object.keys(qsParams)
                  .filter(function(key) {
                      return !!qsParams[key]; //We don't want to have param=undefined fragments in our query string
                  })
                  .map(function(key: string) {
                      return key + '=' + encodeURIComponent(qsParams[key]);
                  })
                  .join('&')
            : '';

        return originalUrl + redirectToQueryString + primaryColor + modalUi + lang + source + gaSessionId + referrer + extraParams;
    };

    const loadingListener = function(e) {
        if (!e.data || !e.data.command) {
            return;
        }

        switch (e.data.command) {
            case 'tipser.widget.close':
                close();

                if (e.data.closedOnThankYouPage) {
                    const orderId = e.data.orderId;
                    window.postMessage({ command: 'tipser.widget.thankYouPageClosed', orderId: orderId }, '*');
                }

                break;
        }
    };

    //Get last active dialog
    const hasDialogs = function() {
        return activeDialogs.length > 0;
    };

    const getLastActiveDialog = function() {
        if (!hasDialogs()) {
            return undefined;
        }

        return activeDialogs[activeDialogs.length - 1];
    };

    //Get last active overlay
    const hasOverlays = function() {
        return activeOverlays.length > 0;
    };

    const getLastActiveOverlay = function() {
        if (!hasOverlays()) {
            return undefined;
        }

        return activeOverlays[activeOverlays.length - 1];
    };

    //Center dialog
    const centerLastActiveDialog = function(activeDialog: any, config: TipserSdkConfigCore) {
        const { left, right, top, bottom } = config.position;

        if (window.innerWidth <= MOBILE_MAX_WIDTH) {
            activeDialog.style.left = '5px';
            activeDialog.style.right = '5px';
            activeDialog.style.top = '5px';
            activeDialog.style.bottom = '5px';
        } else {
            if (left) {
                activeDialog.style.left = left;
            }
            if (right) {
                activeDialog.style.right = right;
            }
            if (top) {
                activeDialog.style.top = top;
            }
            if (bottom) {
                activeDialog.style.bottom = bottom;
            }
        }
    };

    const createDialog = function(config: TipserSdkConfigCore) {
        const dialogId = activeDialogs.length;
        const overlayId = activeOverlays.length;

        //Creating the overlay over full screen
        const overlay = document.createElement('div');
        overlay.setAttribute('class', 'tipser-overlay');

        activeOverlays[overlayId] = overlay;
        isDialogActive = true;
        document.body.appendChild(overlay);

        const dialog = document.createElement('div');
        dialog.setAttribute('class', 'tipser-modal-iframe');
        dialog.style.position = 'fixed';
        dialog.style.zIndex = overlay.style.zIndex + 1;
        overlay.appendChild(dialog);

        const dialogBody = document.createElement('div');
        dialogBody.setAttribute('class', 'tipser-modal-body');
        dialog.appendChild(dialogBody);

        activeDialogs[dialogId] = dialog;

        if (!initialized) {
            window.addEventListener('keydown', (e: KeyboardEvent) => {
                if (e.keyCode == 27) {
                    close();
                }
            });

            bodyStyle = document.body.style.overflow;
            document.body.style.overflow = 'hidden';
            initialized = true;
        }

        // uglyhack for ios7 scrolling bug
        if (isMobileSafari) {
            hideAllBodyChildNodes(overlay);
        }

        centerLastActiveDialog(dialog, config);
        dialogBody.style.height = '100%';
        return dialogBody;
    };

    const appendIframe = function(body: Element, src: string, tipserId: string, widgetUrlPrefix: string, config: TipserSdkConfigCore) {
        const loadingContainer = document.createElement('div');
        loadingContainer.className = 'tipser-modal-loading';

        const spinner = document.createElement('div');
        spinner.className = 'tipser-modal-spinner';
        loadingContainer.appendChild(spinner);

        const iframe = document.createElement('iframe');
        iframe.onload = evt => loaded(evt, config);
        iframe.setAttribute('frameborder', '0');
        iframe.setAttribute('width', '100%');
        iframe.setAttribute('height', '100%');
        iframe.setAttribute('style', 'margin:0;padding:0;display:block;');
        iframe.src = getTipserFrontendUrl(config) + widgetUrlPrefix + src;

        body.appendChild(loadingContainer);
        body.appendChild(iframe);
    };

    return {
        config: <TipserSdkConfigCore>null,
        posGaSessionId: <string>null,

        close: close,

        init(config: TipserSdkConfigCore) {
            this.config = config;
        },

        setPosGaSessionId(posGaSessionId: string) {
            this.posGaSessionId = posGaSessionId;
        },

        isActive: function(): boolean {
            return isDialogActive;
        },

        createDialogWithIframe(tipserId: string, src: string, qsParams: object = {}, from: string = undefined) {
            const body = createDialog(this.config);
            const widgetUrlPrefix = tipserId ? '/wi/' + tipserId : '';
            const iframeSrcAlreadyHasQueryString = src.indexOf('?') > -1;
            const extraQueryStringSeparator = iframeSrcAlreadyHasQueryString ? '&' : '?';

            const posGaSessionId = this.posGaSessionId ? this.posGaSessionId : generateFallbackPosGaSessionId();

            src = src + extraQueryStringSeparator + getQueryString(from, posGaSessionId, qsParams, this.config);
            appendIframe(body, src, tipserId, widgetUrlPrefix, this.config);
        },

        //Creating different dialogs
        purchase(posId: string, resume: boolean) {
            this.createDialogWithIframe(posId, '/checkout' + (resume ? '?resume=true' : ''));
        },

        collections(posId: string, friendlyUrl: string) {
            this.createDialogWithIframe(posId, '/' + friendlyUrl);
        },

        search(posId: string, searchString: string) {
            this.createDialogWithIframe(posId, '/shop', {
                query: searchString,
            });
        },

        product(posId: string, productId: string, from: string, collectionItemId: string) {
            const collectionItemIdSuffix = collectionItemId ? '/' + collectionItemId : '';
            this.createDialogWithIframe(posId, '/products/' + productId + collectionItemIdSuffix, {}, from);
        },

        directToCheckout(posId: string, productId: string, from: string) {
            this.createDialogWithIframe(
                posId,
                '/direct-to-checkout',
                {
                    productId: productId,
                    posId: posId,
                },
                from
            );
        },

        thanks(tipser: string, checkoutId: string) {
            this.createDialogWithIframe(tipser, '/checkout/confirmation/' + checkoutId);
        },
    };
};
