import { getTipserFrontendUrl, TipserSdkConfigCore } from './config';
import { CartSizeCache } from './cart-size-cache';
import { generateFallbackPosGaSessionId } from './ga-sessions';
import { getOrCreateIframe } from './iframe-utils';

const POS_ID_DIMENSION = 'dimension1';
const ARTICLE_DIMENSION = 'dimension3';

export interface GaEventPayload {
    eventPayload: {
        hitType: string;
        eventCategory: string;
        eventAction: string;
        eventLabel?: string;
        dimension1?: string;
        dimension2?: string;
        dimension3?: string;
        dimension4?: string;
    };
    ecData?: Array<{ command: string; payload: any }>;
}

export class TipserApiClient {
    private sdkIframe: HTMLIFrameElement;

    private iframeLoadedPromise: Promise<HTMLIFrameElement>;
    private shoppingCartItemsNumberPromise: Promise<number>;
    private canAccessCookiesPromise: Promise<boolean>;

    private resolveCartItemsNumber: any;
    private resolveItemAddedToCart: any;
    private resolveCanAccessCookies: any;
    private rejectItemAddedToCart: any;

    private readonly config: TipserSdkConfigCore;
    cartSizeCache = new CartSizeCache();

    constructor(config: TipserSdkConfigCore) {
        this.config = config;

        this.initPostMessageListener();
        this.ensureSdkIframeIsAvailable();
    }

    private ensureSdkIframeIsAvailable() {
        const referrer = document.referrer ? `referrer=${encodeURIComponent(document.referrer)}` : '';
        const queryParams = referrer ? `?${referrer}` : '';
        const iframeSrc = `${getTipserFrontendUrl(this.config)}/widget/sdk-iframe.html${queryParams}`;
        this.iframeLoadedPromise = getOrCreateIframe(iframeSrc);

        this.iframeLoadedPromise.then(iframe => {
            this.sdkIframe = iframe;
        });
    }

    private initPostMessageListener() {
        window.addEventListener('message', (message: any) => {
            if (!message.data) {
                return;
            }

            const data = message.data;

            switch (message.data.command) {
                case 'tipser.internal.numberOfItemsResponse':
                    if (this.resolveCartItemsNumber) {
                        this.resolveCartItemsNumber(data.numberOfItems);
                    }
                    break;
                case 'tipser.internal.canAccessCookiesResponse':
                    if (this.resolveCanAccessCookies) {
                        this.resolveCanAccessCookies(data.canAccessCookies);
                    }
                    break;
                case 'tipser.internal.addToCartResponse':
                    if (data.error) {
                        if (this.rejectItemAddedToCart) {
                            this.rejectItemAddedToCart(data.error);
                        }
                    } else {
                        if (this.resolveItemAddedToCart) {
                            this.resolveItemAddedToCart();
                        }
                    }

                    break;
            }
        });
    }

    canAccessCookies = (): Promise<boolean> => {
        return this.iframeLoadedPromise.then(() => {
            //Switching to "wait for POST message reply" mode
            this.canAccessCookiesPromise = new Promise(resolve => {
                this.resolveCanAccessCookies = resolve;
            });

            this.sdkIframe.contentWindow.postMessage(
                {
                    command: 'tipser.internal.canAccessCookies',
                },
                '*'
            );

            return this.canAccessCookiesPromise;
        });
    };

    invalidateCartSizeCache() {
        this.cartSizeCache.invalidateCache();
    }

    getCurrentCartSize(): Promise<number> {
        const maybeCachedCartSize = this.cartSizeCache.getCartSizeFromCache();
        if (maybeCachedCartSize != null) {
            return Promise.resolve(maybeCachedCartSize);
        }

        return this.iframeLoadedPromise.then(() => {
            //Switching to "wait for POST message reply" mode
            this.shoppingCartItemsNumberPromise = new Promise(resolve => {
                this.resolveCartItemsNumber = resolve;
            });

            //Requesting POST message reply from the iframe with number of items in cart
            this.sdkIframe.contentWindow.postMessage(
                {
                    command: 'tipser.internal.numberOfItemsRequest',
                },
                '*'
            );

            //TODO: should we introduce some timeout in case reply never arrives
            //to avoid our clients hanging on this call?
            return this.shoppingCartItemsNumberPromise.then(cartSize => {
                this.cartSizeCache.syncCartSizeFromApi(cartSize);
                return cartSize;
            });
        });
    }

    addToCart(productId: string, options: { posData?: string } = {}): Promise<void> {
        //Switching to "wait for POST message reply" mode
        const itemAddedToCartPromise = new Promise<void>((resolve, reject) => {
            this.resolveItemAddedToCart = resolve;
            this.rejectItemAddedToCart = reject;
        });

        const posArticle = this.getPosArticle();
        const finalPosData = options.posData || this.config.posData;

        const posGaSessionId = generateFallbackPosGaSessionId();
        //we need to associate the event with some Tipser GA session to avoid the same problem in Tipser GA.
        //Reusing posGaSessionId seems good enough...
        const gaSessionId = posGaSessionId;

        this.sdkIframe.contentWindow.postMessage(
            {
                command: 'tipser.internal.addToCartRequest',
                posId: this.config.posId,
                posData: finalPosData,
                posArticle,
                productId,
                posGaSessionId,
                gaSessionId,
            },
            '*'
        );

        return itemAddedToCartPromise.then(() => {
            this.cartSizeCache.incrementCachedCartSizeInCache();
        });
    }

    getPosArticle() {
        //standard analytics article value: host and path with trailing slash removed
        return (window.location.host + window.location.pathname).replace(/\/$/, '');
    }

    sendWhispererEvent(payload: any) {
        this.sdkIframe.contentWindow.postMessage(
            {
                command: 'tipser.analytics.whisperer',
                payload,
            },
            '*'
        );
    }

    sendGaEvent(payload: GaEventPayload) {
        const payloadWithBaseDimensions = {
            ...payload,
            eventPayload: {
                [POS_ID_DIMENSION]: this.config.posId,
                [ARTICLE_DIMENSION]: this.getPosArticle(),
                ...payload.eventPayload,
            },
        };
        this.sdkIframe.contentWindow.postMessage(
            {
                command: 'tipser.analytics.ga',
                payload: payloadWithBaseDimensions,
            },
            '*'
        );
    }

    trackProductViewedEvent(productId: string) {
        const posArticle = this.getPosArticle();
        this.iframeLoadedPromise.then(() => {
            this.sdkIframe.contentWindow.postMessage(
                {
                    command: 'tipser.internal.analytics.productImpression',
                    posId: this.config.posId,
                    productId: productId,
                    posArticle,
                },
                '*'
            );
        });
    }

    trackShopViewedEvent() {
        const posArticle = this.getPosArticle();
        this.iframeLoadedPromise.then(() => {
            this.sdkIframe.contentWindow.postMessage(
                {
                    command: 'tipser.internal.analytics.shopImpression',
                    posId: this.config.posId,
                    posArticle,
                },
                '*'
            );
        });
    }

    trackListClickedEvent(listName: string) {
        const posArticle = this.getPosArticle();
        this.iframeLoadedPromise.then(() => {
            this.sdkIframe.contentWindow.postMessage(
                {
                    command: 'tipser.internal.analytics.listClicked',
                    posId: this.config.posId,
                    listName: listName,
                    posArticle,
                },
                '*'
            );
        });
    }
}
